import '../stylesheets/main.scss';
import Flickity from "flickity";
import LazyLoad from "vanilla-lazyload";
import initTriggerHoverElms from "./trigger-hover-elements";
import { setCookie, getCookie } from './utils';

// Cookie consent
var cookieConsent = document.getElementById("cookie-consent");
var cookieConsentBtn = cookieConsent.getElementsByClassName('js-accept-cookies');

if (getCookie('consent') === 'true') {
    cookieConsent.setAttribute('aria-hidden', 'true');
} else {
    cookieConsentBtn[0].addEventListener('click', event => {
        setCookie('consent', 'true', 365);
        cookieConsent.setAttribute('aria-hidden', 'true');
    });
}

window.addEventListener('load', event => {
    console.log("Window loaded");

    let lazyLoadInstance = new LazyLoad();

    initTriggerHoverElms('js-trigger-hover');
});
